<h1 id="docker-basics" tabindex="-1"><a class="header-anchor" href="#docker-basics" aria-hidden="true">#</a> Docker Basics</h1>
<p>Dockerization is a development method that's getting more and more popular, especially for solutions consisting of multiple applications, such as a website consisting of a web application and a database (and possibly a storage service, a frontend application of some kind, an email service, etc.). To run such a solution on your own computer (e.g. for development), you need to have all applications up and running and all of their dependencies installed, which is not easy nor convenient to setup. But with Docker, each application the solution consists of will run in it's own container together will all dependencies required to run it, and Docker also provides convenient methods for these applications to communicate with each other, so getting a Dockerized solution up and running on your own computer is a piece of cake once you have downloaded and installed Docker.</p>
<div class="custom-container danger"><p class="custom-container-title">Use Docker Desktop for Windows!</p>
<p>When this lecture was recorded, there was a legacy version of Docker called <em>Docker Toolbox</em>, and a newer version of Docker called <em>Docker Desktop for Windows</em>. Using the newer version is of course better, but it couldn't be used on computers running Windows 10 Home, which most students had.</p>
<p>In March 2020, <a href="https://www.docker.com/blog/docker-desktop-for-windows-home-is-here/" target="_blank" rel="noopener noreferrer">Docker released a new version of Docker Desktop for Windows<OutboundLink/></a> that also works on Windows 10 Home using <em>Windows Subsystem for Linux</em>. So nowadays all Windows users can use Docker Desktop for Windows, and forget about Docker Toolbox.</p>
<p>When using Docker through Windows Subsystem for Linux, it's better to place your docker projects on the file system for the Linux distribution you are running (e.g. Ubuntu), and run docker through the Linux distribution, instead of using the PowerShell in Windows (for details, see <a href="https://docs.docker.com/desktop/windows/wsl/#best-practices" target="_blank" rel="noopener noreferrer">Docker Desktop WSL 2 backend Best practices<OutboundLink/></a>).</p>
